.ico {
  position: relative;
  display: inline-block;
  transition: all .3s ease;

  &-logo {
    @include size(300px, 30px);
    background-image: svg-load('logo.svg');
  }

  &-check {
    @include size(20px, 14px);
    background-image: svg-load('check.svg', fill=#00b402);
  }

  &-loupe {
    @include size(20px);
    background-image: svg-load('loupe.svg', fill=#adb1b9);
  }

  &-bell {
    @include size(18px, 20px);
    background-image: svg-load('bell.svg', fill=#adb1b9);
  }

  &-watch {
    @include size(20px);
    background-image: svg-load('watch.svg', fill=#adb1b9);
  }

  &-pause {
    @include size(18px);
    background-image: svg-load('pause.svg', fill=#00b402);
  }

  &-chevron {
    @include size(10px, 7px);
    background-image: svg-load('chevron.svg', fill=#00b402);

    &.gray {
      background-image: svg-load('chevron.svg', fill=rgb(173, 177, 185));
    }

    &.left {
      transform: rotate(90deg);
    }

    &.right {
      transform: rotate(-90deg);
    }
  }

  &-star {
    @include size(20px);
    background-image: svg-load('star.svg', fill=#adb1b9);

    &.white {
      background-image: svg-load('star.svg', fill=#fff);
    }

    &.green {
      background-image: svg-load('star.svg', fill=#21aa00);
    }
  }

  &-play-in-circle {
    @include size(20px);
    background-image: svg-load('play-in-circle.svg', fill=#adb1b9);
  }

  &-article {
    @include size(16px, 20px);
    background-image: svg-load('article.svg', fill=#adb1b9);
  }

  &-book {
    @include size(16px, 20px);
    background-image: svg-load('book.svg', fill=#adb1b9);
  }

  &-presentation {
    @include size(60px, 50px);
    background-image: svg-load('presentation.svg', fill=#adb1b9);
  }

  &-manual {
    @include size(60px, 50px);
    background-image: svg-load('manual.svg', fill=#adb1b9);
  }

  &-mail {
    @include size(20px, 16px);
    background-image: svg-load('mail.svg', fill=#adb1b9);
  }

  &-phone {
    @include size(14px, 20px);
    background-image: svg-load('phone.svg', fill=#adb1b9);
  }

  &-geotag {
    @include size(18px, 20px);
    background-image: svg-load('geotag.svg', fill=#adb1b9);
  }

  &-microphone {
    @include size(16px, 20px);
    background-image: svg-load('microphone.svg', fill=#adb1b9);
  }
}
